import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes: [{
        path: '/',
        component: () =>
            import('@/views/dashboard/Index'),
        children: [
            // Dashboard
            {
                name: 'Dashboard',
                path: '',
                component: () =>
                    import('@/views/dashboard/Dashboard'),
            },
            // Listar Usuarios
            {
                name: 'Listar Usuarios',
                path: '/usuarios',
                component: () =>
                    import('@/views/usuarios/listarUsuarios'),
            },
            // Crear Usuario
            {
                name: 'Crear Usuario',
                path: '/usuarios/nuevo',
                component: () =>
                    import('@/views/usuarios/crearUsuario'),
            },
            // Editar Usuario
            {
                name: 'Actualizar Usuario',
                path: '/usuarios/editar/:id',
                component: () =>
                    import('@/views/usuarios/editarUsuario'),
            },
            // Dashboard ADV
            {
                name: 'Menu ADV',
                path: '/adv',
                component: () =>
                    import('@/views/adv/dashboard'),
            },
            // Listar Vehiculos
            {
                name: 'Lista Vehiculos',
                path: '/adv/vehiculos',
                component: () =>
                    import('@/views/adv/vehicle/listarVehiculos'),
            },
            // Crear Vehiculos
            {
                name: 'Crear Vehiculo',
                path: '/adv/vehiculos/nuevo',
                component: () =>
                    import('@/views/adv/vehicle/crearVehiculo'),
            },
            // Editar Vehiculos
            {
                name: 'Editar Vehiculo',
                path: '/adv/vehiculos/editar/:id',
                component: () =>
                    import('@/views/adv/vehicle/editarVehiculo'),
            },
            // Listar Expedientes
            {
                name: 'Lista Expedientes',
                path: '/adv/expedientes',
                component: () =>
                    import('@/views/adv/record/listarExpedientes'),
            },
            // Crear Expediente
            {
                name: 'Crear Expediente',
                path: '/adv/expedientes/nuevo',
                component: () =>
                    import('@/views/adv/record/crearExpediente'),
            },
            // Editar Expediente
            {
                name: 'Editar Expediente',
                path: '/adv/expedientes/editar/:id',
                component: () =>
                    import('@/views/adv/record/editarExpediente'),
            },
            // Listar Venta
            {
                name: 'Lista Ventas',
                path: '/adv/ventas',
                component: () =>
                    import('@/views/adv/ventas/listarVentas'),
            },
            // Crear Venta
            {
                name: 'Crear Venta',
                path: '/adv/ventas/nuevo',
                component: () =>
                    import('@/views/adv/ventas/crearVenta'),
            },
            // Editar Venta
            {
                name: 'Editar Venta',
                path: '/adv/ventas/editar/:id',
                component: () =>
                    import('@/views/adv/ventas/editarVenta'),
            },
            // Vista Venta
            {
                name: 'Vista Venta',
                path: '/adv/ventas/vista/:id',
                component: () =>
                    import('@/views/adv/ventas/vistaVenta'),
            },
            // Dashboard Ventas
            {
                name: 'Menu Ventas',
                path: '/ventas',
                component: () =>
                    import('@/views/ventas/dashboard'),
            },
            // Listar Campañas
            {
                name: 'Lista Campañas',
                path: '/ventas/campañas',
                component: () =>
                    import('@/views/ventas/campaign/listarCampania'),
            },
            // Crear Campaña
            {
                name: 'Crear Campaña',
                path: '/ventas/campañas/nuevo',
                component: () =>
                    import('@/views/ventas/campaign/crearCampania'),
            },
            // Editar Campaña
            {
                name: 'Editar Campaña',
                path: '/ventas/campañas/editar/:id',
                component: () =>
                    import('@/views/ventas/campaign/editarCampania'),
            },
            // Listar Financiamiento
            {
                name: 'Lista Financiamiento',
                path: '/ventas/financiamiento',
                component: () =>
                    import('@/views/ventas/financing/listarFinanciamiento'),
            },
            // Crear Financiamiento
            {
                name: 'Crear Financiamiento',
                path: '/ventas/financiamiento/nuevo',
                component: () =>
                    import('@/views/ventas/financing/crearFinanciamiento'),
            },
            // Editar Financiamiento
            {
                name: 'Editar Financiamiento',
                path: '/ventas/financiamiento/editar/:id',
                component: () =>
                    import('@/views/ventas/financing/editarFinanciamiento'),
            },
            // Listar Clientes
            {
                name: 'Lista Clientes',
                path: '/ventas/clientes',
                component: () =>
                    import('@/views/ventas/customer/listarClientes'),
            },
            // Crear Cliente
            {
                name: 'Crear Cliente',
                path: '/ventas/clientes/nuevo',
                component: () =>
                    import('@/views/ventas/customer/crearCliente'),
            },
            // Editar Cliente
            {
                name: 'Editar Cliente',
                path: '/ventas/clientes/editar/:id',
                component: () =>
                    import('@/views/ventas/customer/editarCliente'),
            },
            // Listar Accesorios
            {
                name: 'Lista Accesorios',
                path: '/ventas/accesorios',
                component: () =>
                    import('@/views/ventas/props/listarAccesorios'),
            },
            // Crear Accesorio
            {
                name: 'Crear Accesorio',
                path: '/ventas/accesorios/nuevo',
                component: () =>
                    import('@/views/ventas/props/crearAccesorio'),
            },
            // Editar Accesorio
            {
                name: 'Editar Accesorio',
                path: '/ventas/accesorios/editar/:id',
                component: () =>
                    import('@/views/ventas/props/editarAccesorio'),
            },
            // Listar Vendedores
            {
                name: 'Lista Vendedores',
                path: '/ventas/vendedores',
                component: () =>
                    import('@/views/ventas/seller/listarVendedores'),
            },
            // Crear Vendedor
            {
                name: 'Crear Vendedor',
                path: '/ventas/vendedores/nuevo',
                component: () =>
                    import('@/views/ventas/seller/crearVendedor'),
            },
            // Editar Vendedor
            {
                name: 'Editar Vendedor',
                path: '/ventas/vendedores/editar/:id',
                component: () =>
                    import('@/views/ventas/seller/editarVendedor'),
            },
            // Pages
            {
                name: 'User Profile',
                path: 'pages/user',
                component: () =>
                    import('@/views/dashboard/pages/UserProfile'),
            },
            {
                name: 'Notifications',
                path: 'components/notifications',
                component: () =>
                    import('@/views/dashboard/component/Notifications'),
            },
            {
                name: 'Icons',
                path: 'components/icons',
                component: () =>
                    import('@/views/dashboard/component/Icons'),
            },
            {
                name: 'Typography',
                path: 'components/typography',
                component: () =>
                    import('@/views/dashboard/component/Typography'),
            },
            // Tables
            {
                name: 'Regular Tables',
                path: 'tables/regular-tables',
                component: () =>
                    import('@/views/dashboard/tables/RegularTables'),
            },
            // Maps
            {
                name: 'Google Maps',
                path: 'maps/google-maps',
                component: () =>
                    import('@/views/dashboard/maps/GoogleMaps'),
            },

        ],
    }],
})
